import React from 'react';
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import info from './info';

const Specificdate = () => {
    const {id} = useParams();
    const [selectedDay] = info.filter((value) => {
        return value.id.toString() === id
    })

    return (
        <>
        { selectedDay && <>
        <h1 style={{textAlign: 'center', fontSize: '7rem'}}>{selectedDay.title}</h1>
        <div style={{width: '60%', textAlign: 'justify', margin: '1% 20%', fontSize: '3rem'}}>
            <p>{selectedDay.text}</p>
            <Link to="/calendario">Retornar ao calendário</Link>
        </div>
        </> || <div style={{fontSize: '5rem', margin: '200px 1000px', textAlign: 'center'}}>
                        <h1>Página não encontrada</h1>
                        <Link to='/'>Retornar a página inicial</Link>
                    </div>
        }
        </>
    );

}

export default Specificdate;