import React from 'react';
import { Card } from 'react-bootstrap';
import insta from '../imgs/instagram.png';
import face from '../imgs/facebook.png';
import mail from '../imgs/mail.png';

const Socialcard = ({type, text, url}) => {
    var imag;
    if (type === "INS") {imag = insta;}
    if (type === "FAC") {imag = face;}
    if (type === "MAI") {imag = mail;}

    return (
        <Card style={{width:'15rem', backgroundColor: 'inherit', border: '0px', marginTop: '30px',}}>
            <Card.Link href={url} target="_blank">
                <Card.Img variant='top' src={imag}/> 
            </Card.Link>
            <Card.Body>
                <Card.Text style = {{textAlign: "center", fontSize:'1.45rem'}}>
                    <strong>{text}</strong>
                </Card.Text>
            </Card.Body>
        </Card>
    );
}

export default Socialcard;
