import React, { useState } from 'react';
import { Pagination } from 'react-bootstrap';
import Interactive from './makinginter/interactive';

const Calslider = ({selected}) => {
    const [photo, setPhoto] = useState(1);
    const months = ['AGOSTO', 'SETEMBRO', 'OUTUBRO', 'NOVEMBRO', 'DEZEMBRO', 'JANEIRO']

    let items = [];
    for (let number = 1; number <= 6; number++) {
        items.push(
            <Pagination.Item key={number} active={number === photo} onClick={() => setPhoto((actual) => {
                setPhoto(number)
            })}>
                {number}
            </Pagination.Item>,
            );
        }

    return (
        <>
        <div>
            <h1 style={{textAlign: 'center', fontSize: '5rem'}}>{months[photo - 1]}</h1>
            <Interactive activemonth={photo}></Interactive>
            <div style={{display: 'flex', justifyContent: 'center'}}>
                <Pagination size='lg'> {items} </Pagination>
            </div>
        </div>  
        </>
    );
}

export default Calslider;