import React from 'react';
import logo from '../imgs/logocap.png'
import { Card } from 'react-bootstrap';

const Home = () => {

    return(
        <>
        <h1 style={{textAlign: 'center', fontSize: '5rem'}}>BEM VINDO</h1>
        <div className="homemain" style={{marginTop: '3%'}}>
            <div className="info">
                Este é o site do capítulo Templários de Itabira n°722. Por esse site temos o intuito de facilitar o acesso ao calendário e o contato com o capítulo. Obrigado pelo acesso!
            </div>
            <div>
                <Card bg='dark' style={{border: '10px solid black', borderRadius: '8px'}}>
                    <Card.Body>
                        <div style={{display: 'flex', justifyContent:'center'}}>
                            <img src={logo} alt="" style={{maxWidth: '560px', maxHeight: '370px'}} />
                        </div>
                        <Card.Text style={{color:'white', textAlign: 'center', fontSize: '3rem'}}>
                            BRASÃO DO CAPÍTULO
                        </Card.Text>
                    </Card.Body>
                </Card>
            </div>
        </div>
        </>
    );
}

export default Home;