import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import logo from '../imgs/logocap.png';

const TheNavBar = () => {
    return (
        <>
        <Navbar expand="lg" bg="dark" variant="dark">
            <Navbar.Brand>
                <img src={logo} alt="Templários de Itabira" width="100px" height="100px"/>
            </Navbar.Brand>

            <Nav>
                <Nav.Link href="/" className="aumentafonte">
                    Sobre
                </Nav.Link>
                <Nav.Link href="/contato" className="aumentafonte">
                    Contato
                </Nav.Link>
                <Nav.Link href="/calendario" className="aumentafonte">
                    Calendário
                </Nav.Link>
            </Nav>

            <Navbar.Collapse className="justify-content-end">
                <h1 style={{fontFamily: "Impact", fontStyle: "italic"}}>GESTÃO 2021/2</h1>
            </Navbar.Collapse>
        </Navbar>
        
        </>
    );
}

export default TheNavBar;