import React from 'react';
import Credits from '../components/credits.jsx';
import Socialcard from '../components/socialcard.jsx';


const Contact = () => {

    return (
        <>
        <h1 style={{textAlign: "center", fontSize: '5rem'}}>CONTATOS</h1>
            <div className="fcontainer" style={{marginTop: '2%'}}>
                <div className="fitem">
                    <Socialcard type="INS" text="@templariosdeita" url="https://www.instagram.com/templariosdeita/"/>
                </div>

                <div className="fitem">
                    <Socialcard type="FAC" text="Templários de Itabira" url="https://www.facebook.com/CapituloTemplarioDeItabiraNo722"/>
                </div>

                <div className="fitem">
                    <Socialcard type="MAI" text="templariosdeita@gmail.com"/>
                </div>           
            </div>
        <div style={{position: 'fixed', bottom: '0', right: '0'}}>
            <Credits/>
        </div>
        </>
    );
}

export default Contact;
