import React from 'react';
import info from './info.jsx'
import { Link } from 'react-router-dom';

//Para trocar duração do mês, basta apenas mudar no 'Array(x)' abaixo

const Interactive = ({activemonth}) => {
    var calendar = [
        Array(30).fill(""),
        Array(30).fill(""),
        Array(30).fill(""),
        Array(30).fill(""),
        Array(30).fill(""),
        Array(30).fill(""),
    ];

    for (let i=0; i < info.length; i++) {
        var curobj = info[i];
        var {day, month} = curobj;
        calendar[month - 1][day - 1] = curobj;
    }

    var selectedMonth = calendar[activemonth - 1];
    return (
        <>
        <div className="calbox">
            {
            selectedMonth.map((day, counter) => {
                return (
                    <div className="calboxson" key={counter}> 
                        {counter % 31 + 1} <br />
                        <div style={{paddingTop: '30px'}}>
                            <Link to={`/calendario/${day.id}`}><p style={{fontSize: '1.3rem'}}> <strong>{day.title}</strong> </p></Link>
                        </div>
                    </div>
                );
            })
            }
        </div>
        </>
    );
}


export default Interactive
